import { FiendGame as FG } from "../engine/FiendGame";

export class Enemy {
  public HP: number;
  public ATK: number;
  public speed: number;
  public position: { x: number, y: number };

  constructor() {
    this.HP = 100;
    this.ATK = 1;
    this.speed = 100;
    this.position = {
      x: 0,
      y: 0
    };
  }

  update(delta: number) {
    // FG.gameObjects.push(new Enemy());
    // console.log('FG :', FG);
    // console.log('FG.gameObjects :', FG.gameObjects);


    this.position.x += Math.floor(Math.random() * Math.floor(100)) * delta;
    this.position.y += Math.floor(Math.random() * Math.floor(100)) * delta;
  }

  draw(ctx: CanvasRenderingContext2D) {
    ctx.beginPath();
    ctx.arc(this.position.x, this.position.y, 10, 0, Math.PI*2);
    ctx.fillStyle = "#0095DD";
    ctx.fill();
    ctx.closePath();
  }
}
